let lugin = {
    install: function (Vue, Options) {
        Vue.prototype.formatDate = function (num) {
            var oDate = new Date(num),
            Year = oDate.getFullYear(),
            Month = oDate.getMonth() + 1,
            Day = oDate.getDate(),
            Hour = oDate.getHours(),
            Min = oDate.getMinutes(),
            Sen = oDate.getSeconds(),
            Time = Year + '-' + addZero(Month) + '-' + addZero(Day) + '-' + addZero(Hour) + '-' + addZero(Min) + '-' + addZero(Sen);
            return Time;
        }
        // Vue.prototype.add=function(){
        //     alert(1)
        // }
        function addZero(num) {
            if (parseInt(num) < 10) {
                num = "0" + num
            }
        }
        Vue.filter("formatDate", function(val) {
            var oDate = new Date(val),
            Year = oDate.getFullYear(),
            Month = oDate.getMonth() + 1,
            Day = oDate.getDate(),
            Hour = oDate.getHours(),
            Min = oDate.getMinutes(),
            Sen = oDate.getSeconds();
            // if (Hour > 12) {
            //     Hour = "下午" + (Hour - 12)
            // } else if (Hour == 12) {
            //     Hour = "中午" + Hour
            // } else {
            //     Hour = "下午" + Hour
            // }
            var Time = Year + '-' + addZero(Month) + '-' + addZero(Day) + '' + addZero(Hour) + '-' + addZero(Min) + '-' + addZero(Sen)
            return Time
        })
        
    }
}
export default lugin


