<template>
  <div class="goodslist">
    <div class="goodstitle">
      <h1>{{this.$route.meta.title}}</h1>
      <el-button type="primary" icon="el-icon-plus">添加商品</el-button>
    </div>
    <div class="goodsseek">
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-input v-model="input" placeholder="请输入内容"></el-input>
      <el-button @click="query">查询</el-button>
    </div>
    <div class="goodscontent">
      <table class="table-bordered">
        <thead>
          <tr>
            <th width="5%">id</th>
            <th width="60%">信息</th>
            <th width="10%">价格</th>
            <th width="15%">状态</th>
            <th width="10%">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(itme,key) in list">
            <td width="5%">{{itme.id}}</td>
            <td width="45%">
              <p>{{itme.name}}</p>
              <p>{{itme.subtitle}}</p>
            </td>
            <td width="10%">{{itme.price}}</td>
            <td width="15%">
              <span>{{itme.status==1?"在售":"已下架"}}</span>
              <p @click="diji(itme,key)" class="p1" v-if="itme.status==1">下架</p>
              <p @click="diji(itme,key)" class="p1" v-else>上架</p>
            </td>
            <td width="15%">
              <span>查看</span>
              <span>编辑</span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <Pagination :total="total" @paginvalue="Pagination"></Pagination>

    <!-- <p class="pp" v-if="arr">list数组里面有数据所以我显示了</p>
    <p class="pp" v-else> list数组没有数据所以我不显示</p> -->
  </div>
</template>

<script>
import { goodslist } from "@/request/http.js";
import { statuslist } from "@/request/http.js";
import { seeklist } from "@/request/http.js";
import { seeklist1 } from "@/request/http.js";
import { goodsPagination } from "@/request/http.js";
import Pagination from "@/components/Pagination";
export default {
  name: "Goodslist",
  components: {
    Pagination
  },
  data() {
    return {
      input: "",
      value: "",
      options: [
        {
          value: "1",
          label: "按商品id查询"
        },
        {
          value: "2",
          label: "按商品名称查询"
        }
      ],
      list: [],
      total: 0
    };
  },
  computed: {
    
  },
  mounted() {
    this.goodslist();
  },
  methods: {
    query() {
      if (this.value == 1) {
        console.log("按id查询");
        var productId = {
          productId: this.input
        };
        seeklist(productId).then(res => {
          console.log(res.data.data);
          this.list = res.data.data.list;
          this.total = res.data.data.total;
        });
         if (this.input == "") {
          this.list = [];
          this.total = 0;
          return false;
        }
      } else {
        console.log("按商品名查询");
        var obj = {
          productName: this.input
        };
        seeklist1(obj).then(res => {
          console.log(res.data.data);
          this.list = res.data.data.list;
          this.total = res.data.data.total;
        });
        if (this.input == "") {
          this.list = [];
          this.total = 0;
          return false;
        }
      }
      this.goodslist();
    },
    goodslist() {
      goodslist.then(res => {
        console.log(res.data.data.total);
        this.total = res.data.data.total;
        this.list = res.data.data.list;
      });
    },
    diji(itme, k) {
      var txt = "";
      if (this.list[k].status == 1) {
        txt = "下架";
      } else {
        txt = "上架";
      }
      this.$confirm("确认要" + txt + "该商品吗", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(() => {
          if (this.list[k].status == 1) {
            this.list[k].status = 2;
          } else {
            this.list[k].status = 1;
          }
          this.$message({
            type: "success",
            message: "修改产品状态成功"
          });

          statuslist({ productId: itme.id, status: this.list[k].status }).then(
            res => {
              if (
                res.data.status == 0 ||
                res.data.status == 1 ||
                res.data.status == 2
              ) {
                this.goodslist();
                console.log(itme.status);
              }
              console.log(this.list[k].status);
            }
          );
        })
        .catch(() => {
          //  this.$message({
          //   type: 'info',
          //   message: '已取消该操作'
          // });
          this.$message("已取消该操作");
        });
    },
    Pagination(val) {
      console.log(val, "父组件接收");
      goodsPagination(val).then(data => {
        console.log(data.data.data, "data数据");
        data = data.data.data;
        this.list = data.list;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/Scss/index.scss";
.goodstitle {
  @include Size(100%, 80px);
  @include FlexSpace();
  @include Pd(1%);
  box-sizing: border-box;
  .el-button {
    background-color: #337ab7;
    border-color: #2e6da4;
  }
}
.goodsseek {
  width: 40%;
  @include FlexSpace();
  .el-input {
    width: 35%;
  }
}
.goodscontent {
  width: 100%;
  // border: 1px solid red;
  .table-bordered {
    width: 100%;
    border: 1px solid #ddd;
    border-spacing: 0;
    border-collapse: collapse;
    margin-top: 20px;
    thead {
      width: 100%;
      tr {
        width: 100%;
        th {
          text-align: left;
          padding: 1%;
          border: 1px solid #ddd;
        }
      }
    }
    tbody {
      width: 100%;
      tr {
        width: 100%;
        td {
          padding: 1%;
          border: 1px solid #ddd;
          .p1 {
            display: inline-block;
            width: 40px;
            text-align: center;
            height: 20px;
            font-size: 14px;
            border-radius: 2px;
            color: #fff;
            background-color: #f0ad4e;
            border-color: #eea236;
          }
        }
      }
    }
  }
}
.goodslist {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.pp {
  width: 100%;
  height: 100%;
  background: red;
  color: black;
  font-size: 100px;
}
</style>


