<template>
  <div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="10"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
    props:{
        total:Number
    },
    data () {
        return {
            currentPage:1
        }
    },
    methods:{
        handleCurrentChange(val){
             console.log(val)
             this.$emit("paginvalue",val)
        }
    }
};
</script>

<style lang="scss" scoped>
</style>


