<template>
  <div class="home">
    <h1>{{this.$route.meta.title}}</h1>
    <div class="home-box">
      <div  class="itme">
        <p>{{this.obj.orderCount}}</p>
        <p>
          <i class="el-icon-user-solid"></i>
          <span>用户总数</span>
        </p>
      </div>
      <router-link lag>

      </router-link>
      <div class="itme">
        <p>{{this.obj.productCount}}</p>
        <p>
          <i class="el-icon-s-fold"></i>
          <span>商品总数</span>
        </p>
      </div>
      <div class="itme">
        <p>{{this.obj.userCount}}</p>
        <p>
          <i class="el-icon-document-checked"></i>
          <span>订单总数</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { HomeRight } from "@/request/http.js";
export default {
  name: "Home",
  data() {
    return {
      obj: {}
    };
  },
  mounted() {
    this.HomeRight();
  },
  methods: {
    HomeRight() {
      HomeRight.then(res => {
        console.log(res.data);
        this.obj = res.data.data;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/Scss/index.scss";

h1 {
  padding: 1%;
}
.home-box {
  .itme {
    background: #f0ad4e;
    border: 1px solid #f0ad4e;
    transition: all 0.1s ease;
    cursor: pointer;
    p:nth-of-type(1) {
      @include Size(100%, 40px);
      @include FontSize(40px);
      text-align: center;
      margin-top: 30px;
      color: white;
    }
    p:nth-of-type(2) {
      @include Size(100%, 40px);
      @include FontSize(20px);
      text-align: center;
      margin-top: 30px;
      color: white;
    }
  }
  .itme:nth-of-type(2) {
    background-color: #5cb85c;
    border: 1px solid #5cb85c;
  }
  .itme:nth-of-type(3) {
    background-color: #4cb1cf;
    border: 1px solid #4cb1cf;
  }
  .itme:hover {
    transform: scale(1.05);

    p:nth-of-type(1) {
      color: black;
    }
    p:nth-of-type(2) {
      color: black;
    }
   
  }
}
@media screen and (min-width: 900px) {
  .home {
    @include Size(100%, 100%);
    .home-box {
      @include Size(100%, 40%);
      margin-top: 30px;
      @include FlexSpace();
    
      .itme {
        @include Size(400px, 200px);
      }
    }
  }
}
@media screen and (max-width: 850px) {
  .home {
    @include Size(100%, 100%);
    .home-box {
      @include Size(100%, 100%);

      .itme {
        @include Size(90%, 200px);
        // margin-top: 20px; 
        // margin-left:20px;
        margin: 20px auto;
      }
    }
  }
}
</style>


