
import { Request } from "@/util/request.js"

//获取接口的公共的配置文件

//登录接口
export const login = (obj,data) => Request.getData({
    url : `/api/user/login.do?username=${obj.username}&password=${obj.password}`,
    method : "post",
    // data
})



//首页接口
export const HomeRight = Request.getData({
    url : `/api/statistic/base_count.do`,
    method : "get",
    // data
})
//商品管理
export const goodslist = Request.getData({
    url : `/api/product/list.do`,
    method : "get",
    // data
})
//商品管理  上下架
export const statuslist=(data)=> Request.getData({
    url : `/api/product/set_sale_status.do?productId=${data.productId}&status=${data.status}`,
    method : "get",
    // data
})
//搜索按id
export const seeklist=(data)=> Request.getData({
    url : `/api/product/search.do?productId=${data.productId}`,
    method : "get",
    // data   productName=${data.productName}&
})
//搜索按name
export const seeklist1=(data)=> Request.getData({
    url : `/api/product/search.do?productName=${data.productName}`,
    method : "get",
    // data   productName=${data.productName}&
})
//商品管理分页
export const goodsPagination =data=>Request.getData({
    url:`/api/product/list.do?pageNum=${data}`,
    method:"post"
})
//用户接口
export const userList =()=> Request.getData({
    url : `/api/user/list.do`,
    method : "post",
    // data
})
//分页
export const List =data=>Request.getData({
    url:`/api/user/list.do?pageNum=${data}`,
    method:"post"
})
//订单接口
export const OrderList =()=> Request.getData({
    url : `/api/order/list.do`,
    method : "post",
    // data
})
//订单分页接口
export const OrderPagination =data=>Request.getData({
    url:`/api/order/list.do?pageNum=${data}`,
    method:"post"
})


