<template>
  <div class="Orderlist">
    <div class="Ordertitle">
      <h1>{{this.$route.meta.title}}</h1>
    </div>
    <div class="Orderseek">
      <el-select v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-input v-model="input" placeholder="请输入内容"></el-input>
      <el-button>查询</el-button>
    </div>
    <el-table :data="list" style="width: 100%" border>
      <el-table-column prop="orderNo" label="订单号" width="180"></el-table-column>
      <el-table-column prop="receiverName" label="收件人" width="180"></el-table-column>
      <el-table-column prop="statusDesc" label="订单状态"></el-table-column>
      <el-table-column prop="payment" label="订单总价">
        <template slot-scope="scope">￥{{ scope.row.payment }}</template>
      </el-table-column>
      <el-table-column prop="createTime" label="注册时间">
        <template slot-scope="scope">{{ scope.row.createTime | dateFormat }}</template>
      </el-table-column>
      <el-table-column  label="操作"><a href=""><el-button type="text">查看</el-button></a></el-table-column>
    </el-table>
    <Pagination :total="total" @paginvalue="Pagination"></Pagination>
  </div>
</template>

<script>
import { OrderList } from "@/request/http";
import { OrderPagination } from "@/request/http";
import Pagination from "@/components/Pagination";
export default {
  name: "Orderlist",
  components: {
    Pagination
  },
  data() {
    return {
      list: [],
      total: 0,
      input: "",
      value: "",
      options: [
        {
          value: "选项1",
          label: "按订单号查询"
        }
      ]
    };
  },
  mounted() {
    this.Orderlist();
  },
  methods: {
    async Orderlist() {
      var res = await OrderList({
        pageNum: 1
      });
      console.log(res.data.data.list);
      this.list = res.data.data.list;
      this.total = res.data.data.total;
    },
    Pagination(val) {
      console.log(val, "父组件接收");
      OrderPagination(val).then(data => {
        console.log(data.data.data, "data数据");
        data = data.data.data;
        this.list = data.list;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/Scss/index.scss";
.Orderlist {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.Orderseek {
  width: 40%;
  @include FlexSpace();
  .el-input {
    width: 35%;
  }
}
.el-table{
    margin-top: 20px
}
</style>


