<template>
  <div class="index">
    <div class="lodd">
      <div class="lodd-left">
        <b>HAPPY</b>
        HAPPY
      </div>
      <ul>
        <li @click="dianji" v-if="!show" @mouseleave="mouseLeave" @mouseover="mouseOver" class="li">
          <i class="el-icon-user-solid"></i>
          <span>欢迎</span>
          <i class="el-icon-caret-bottom"></i>
        </li>
        <li v-else @mouseleave="mouseLeave" @mouseover="mouseOver" class="li">
          <i class="el-icon-user-solid"></i>
          <span>欢迎 admin</span>
          <i class="el-icon-caret-bottom"></i>
        </li>
        <p v-show="flag" class="p1" @click="signout">
          <i class="el-icon-caret-right"></i>
          退出登录
        </p>
      </ul>
    </div>
    <div class="box">
      <div class="left">
        <el-col :span="12">
          <el-menu
            default-active="2"
            class="el-menu-vertical-demo"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            :router="true"
          >
            <el-menu-item index="/index/home">
              <i class="el-icon-s-data"></i>首页
            </el-menu-item>
            <el-submenu index="2">
              <template slot="title">
                <i class="el-icon-s-fold"></i>
                <span>商品</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/index/goodslist">商品管理</el-menu-item>
              </el-menu-item-group>
              <el-menu-item-group>
                <el-menu-item index="/index/categorylist">品类管理</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">
                <i class="el-icon-date"></i>
                <span>订单</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/index/orderlist">订单管理</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title">
                <i class="el-icon-user"></i>
                <span>用户</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="/index/userlist">用户列表</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-col>
      </div>
      <div class="rigth">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      flag: false,
      show: false
    };
  },
  mounted(){
    let data =localStorage.getItem("admin")
    if(data !=null){
      this.show =true
    }
  },
  methods: {
    // 鼠标移入事件
    mouseOver() {
      // alert("周宇飞王八蛋")
      this.flag = true;
    },
    //鼠标移出事件
    mouseLeave() {
      this.$nextTick(() => {
        //延迟1m执行
        setTimeout(() => {
          this.flag = false;
        }, 1000);
      });
    },
    //退出登录事件
    signout() {
      // alert("已退出")
      localStorage.removeItem("admin");
      this.$message("已退出登录");
      this.$router.push("/login")
    },
    dianji(){
      this.$router.push("/login")
    }
  },
  
};
</script>

<style lang="scss" scoped>
@import "@/Scss/index.scss";
.index {
  @include Size(100%, 100%);

  .lodd {
    @include Size(100%, 10%);
    @include FlexSpace();
    box-shadow: 2px 2px 2px #ccc;
    .lodd-left {
      @include SizeBack(218px, 100%, #545c64);
      @include FontOnly(30px);
      // @include Pd(0.1%);
      font-weight: 700;
      text-align: center;
      line-height: 30px;
      padding-top: 30px;

      b {
        color: #2dafcb;
      }
    }
    .li {
      margin-right: 30px;
      height: 80px;
      line-height: 80px;
      // border: 1px solid red;
    }

    .p1 {
      background: white;
      border: 1px solid #ccc;
      padding: 1% 2% 1% 1%;
      position: fixed;
      top: 80px;
      right: 20px;
      z-index: 100;
    }
  }
  .box {
    @include Size(100%, 88%);
    @include FlexSpace();
    .left {
      @include SizeBack(220px, 100%, #545c64);
      // box-sizing: border-box;
      .el-col-12 {
        @include Size(218px, 0);
      }
    }
    .rigth {
      @include Size(88%, 96%);
      @include Pd(1%);
    }
  }
}
</style>


