import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import Home from '@/pages/home/home'
import Goodslist from '@/pages/home/goodslist'
import Category from '@/pages/home/categorylist'
import Order from '@/pages/home/orderlist'
import Userlist from '@/pages/home/userlist'
import Login from '@/pages/login/login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index,
      children: [
        {
          path: "home",
          component: Home,
          meta: {
            title: "首页"
          }
        },
        {
          path: "goodslist",
          component: Goodslist,
          meta: {
            title: "商品管理"
          }
        },
        {
          path: "orderlist",
          component: Order,
          meta: {
            title: "订单管理"
          }
        },
        {
          path: "categorylist",
          component: Category,
          meta: {
            title: "品类管理"
          }
        },
        {
          path: "userlist",
          component: Userlist,
          meta: {
            title: "用户列表"
          }
        },
        {
          path:"/404",
          component:()=>import("../components/404"),
          meta:{title:"404"}
        },
      ]
    },
    {
      path:"/login",
      name:"Login",
      component: Login,
      meta:{
        title:"登录"
      }
    },
    
    {
      path: "/",
      redirect: "/index"
    }
  ]
})



