<template>
  <div class="login">
    <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="login-form">
      <el-form-item prop="username">
        <el-input placeholder="请输入账号" v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="form.password" placeholder="请输入密码"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { login } from "@/request/http.js";
export default {
  name: "Login",
  data() {
    return {
      form: {
        username: "admin",
        password: "admin"
      },
      rules: {
        username: [{ required: true, message: "请输入账号", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  methods: {
    onSubmit(form) {
      login(this.form).then(data => {
        console.log(data);
        if (data.data.status == 0) {
          localStorage.setItem("admin", this.form.username);
          this.$router.push("/");
          this.$message(data.data.msg);
        } else {
          this.$message(data.data.msg);
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/Scss/index.scss";
.login {
  @include Size(100%, 100%);
  position: absolute;
  overflow: hidden;
}
.login-form {
  @include Size(40%, 100%);
  @include Pd(30px);
  margin: 180px auto;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 20px;
  .login-title {
    text-align: center;
    color: #303133;
  }
}
.el-button {
  @include Size(100%, 100%);
}
</style>


